import { Typography } from "@mui/material";
import { Email, Phone } from "@mui/icons-material";
import { ReactComponent as Skype } from "bootstrap-icons/icons/skype.svg";
import { ReactComponent as Discord } from "bootstrap-icons/icons/discord.svg";
import data from "../../../config/data/contactme.json";

const Contact = () => {
  return (
    <div
      id="contact-component"
      data-aos="fade-up"
      className="w-full p-32 relative bg-img bg-gradient-to-b from-black to-[#1a1a1a]"
    >
      <Typography
        variant="h2"
        className="w-full text-center text-gray-100 pb-32 animate__animated animate__fadeInUp"
      >
        Contact Me
      </Typography>

      <div className="flex justify-center ">
        <div className="flex flex-col border border-white border-opacity-10 pl-16 pr-16 bg-white bg-opacity-5 rounded-[10px] p-16">
          <a
            className="flex items-center p-3"
            href="mailto:syed.tech.beast130@gmail.com"
          >
            <Email />
            <Typography variant="h5" className="ml-3 ">
              {data.email}
            </Typography>
          </a>

          <a className="flex items-center p-3" href={data.skpye.link}>
            <Skype width={20} height={20} />
            <Typography variant="h5" className="ml-3 ">
              {data.skpye.displayName}
            </Typography>
          </a>

          <a className="flex items-center p-3" href={data.discord.link}>
            <Discord width={20} height={20} />
            <Typography variant="h5" className="ml-3 ">
              {data.discord.displayName}
            </Typography>
          </a>

          <div className="flex items-center p-3">
            <Phone />
            <Typography variant="h5" className="ml-3 ">
              {data.phone}
            </Typography>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
