import React from "react";
import { FC } from "react";
import { Avatar, Typography } from "@mui/material";
import TechIcons from "../../../@utils/techIcons";
import SkillSetCard from "./SkillSetCard";

const SkillSet: FC = () => {
  return (
    <div
      id = "skillset-component"
      data-aos="fade-up"
      className="w-full p-32 relative bg-img bg-gradient-to-b from-black to-[#1a1a1a]"
    >
      <img src="./imgs/next.svg" className="absolute left-30 top-36" />
      <img src="./imgs/react.svg" className="absolute right-10 top-0" />
      <img src="./imgs/svelte.svg" className="absolute bottom-32 left-10" />
      <img src="./imgs/vue.svg" className="absolute  bottom-8 right-32" />

      <Typography
        variant="h2"
        className="w-full text-center text-gray-100 pb-32 animate__animated animate__fadeInUp"
      >
        My Skill set
      </Typography>

      <div className="flex justify-center z-10">
        <div className="flex border border-white border-opacity-10 pl-16 pr-16 bg-white bg-opacity-5 rounded-[20px]">
          <SkillSetCard data={TechIcons.tech0} className={""}/>
          <SkillSetCard data={TechIcons.tech1} className={"pt-5"} />
          <SkillSetCard data={TechIcons.tech2} className={""}/>
          <SkillSetCard data={TechIcons.tech3} className={"pt-8"}/>
          <SkillSetCard data={TechIcons.tech4} className={""}/>
          <SkillSetCard data={TechIcons.tech5} className={"pt-12"}/>
        </div>
      </div>
    </div>
  );
};

export default SkillSet;
