import { FC } from "react";
import { Card } from "@mui/material";
import styled from "@emotion/styled";

interface ProjectCardType {
  project: Object | null | any;
}

const InnerButton = styled.a`
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(3px);
  color: rgb(240, 240, 240);

  border: 1px solid rgba(255, 255, 255, 0.6);
  padding: 10px 16px 10px 24px;
  font-size: 17px;
  border-radius: 50px 0 0 50px;
  position: absolute;
  right: 0;
  top: 10px;
  font-weight: bolder;
  transition: 0.3s ease;

  &:hover {
    padding-right: 60px;
    padding-left: 20px;
  }
`;

const WhiteShadow = styled.div``;

const ProjectCard: FC<ProjectCardType> = ({ project }) => {
  let { bodyText, image, link, tags, title } = project;

  return (
    <Card className="relative border-white border-opacity-0 h-52 cursor-pointer rounded-lg overflow-hidden">
      <img className="absolute w-full h-full" src={image} />
      <InnerButton href={link} target="_blank" rel="noopener noreferrer">
        Live Demo
      </InnerButton>

      <div className="absolute w-full h-full rotate-45 scale-150 bg-gradient-to-r from-[#FFFFFF4D] right-52"></div>
    </Card>
  );
};

export default ProjectCard;
