import React from "react";
import { FC } from "react";
import { Typography } from "@mui/material";
import AppSVG from "../../../@utils/@shared-components/AppSVG";

const About: FC = () => {
  return (
    <div
      id = "about-component"
      data-aos="fade-up"
      className="w-full h-full flex-col p-16 pl-32 pr-32 bg-[image:radial-gradient(80%_50%_at_50%_-20%,hsl(206,81.9%,65.3%,0.5),rgba(255,255,255,0))] pt-32 overflow-hidden"
    >
      <AppSVG />

      <div className="w-full p-4 text-gray-50">
        <Typography
          variant="h1"
          className="text-center pb-16 opacity-40 animate__animated animate__flipInX"
        >
          Hi, Everyone
        </Typography>
      </div>
      <div className="w-full flex ">
        <div className="w-full flex pr-8 flex-col justify-center items-center">
          <Typography
            variant="h3"
            className="text-gray-100 animate__animated animate__fadeInUp animate__delay-1s"
          >
            I am Jin Brown
          </Typography>

          <Typography
            variant="h4"
            className="text-gray-500 animate__animated animate__fadeInUp animate__delay-2s pt-6"
          >
            I am a senior front-end Developer with over 7 years of hands-on
            experience specializing in crafting high-quality, user-centric web
            applications. I am excited to leverage expertise in contributing to
            innovative projects and delivering impactful solutions in a dynamic,
            fast-paced environment.
          </Typography>
        </div>
      </div>
    </div>
  );
};

export default About;
