import styled from "@emotion/styled";
import React from "react";
import { FC } from "react";

const navigatePaths = {
  about: "#about-component",
  skills: "#skillset-component",
  projects: "#projects-component",
  contact: "#contact-component",
};

const Goto = styled.a`
  transition: 0.6s;
  border: none;
  border-width: 2px;
  border-style: solid;
  border-image: linear-gradient(
      90deg,
      transparent,
      rgba(34, 211, 238, 0.9),
      transparent
    )
    1;

  padding: 5px 12px;

  &:hover {
    color: rgba(34, 211, 238, 0.9);
  }
  margin-left: 4px;
`;

const Header: FC = () => {
  return (
    <div className="w-full text-whtie p-8 flex fixed top-0 justify-end z-20 bg-white bg-opacity-0 backdrop-blur-sm">
      <div className="flex">
        <Goto href={navigatePaths.about}>About</Goto>
        <Goto href={navigatePaths.skills}>Skills</Goto>
        <Goto href={navigatePaths.projects}>Projects</Goto>
        <Goto href={navigatePaths.contact}>Contact</Goto>
      </div>
    </div>
  );
};

export default Header;
