import React from "react";
import { FC } from "react";

interface SkillSetCardType {
  data: Object[];
  className: string | undefined | null | any;
}

const SkillSetCard: FC<SkillSetCardType> = ({ data, className = "" }) => {
  return (
    // <div className={`flex flex-col pt-4 ${className && className}`}>
    <div className={className}>
      {data.map((item, index) => (
        <img
          className={`w-12 h-12 m-6 transition duration-150 ease-in-out cursor-pointer hover:scale-125 ${
            index === 0 || index === data.length - 1 ? "opacity-50" : ""
          }`}
          src={`./imgs/stack-icons/${item}`}
          key={index}
        />
      ))}
    </div>
  );
};

export default SkillSetCard;
