import React, { useState, useEffect, useContext } from "react";
import { Fade } from "react-swift-reveal";
import FallbackSpinner from "./FallbackSpinner";
import ProjectCard from "./ProjectCard";
import data from "../../../config/data/projects.json";
import { Button, Container, Typography } from "@mui/material";

const Projects = () => {
  
  const [showMore, setShowMore] = useState(false);

  const numberOfItems = showMore && data && data ? data.projects.length : 6;

  const showProjectCards = () => {
    return data.projects.slice(0, numberOfItems).map((project) => (
      <Fade key={project.title}>
        <ProjectCard project={project} />
      </Fade>
    ));
  };

  return (
    <div id = "projects-component" className="w-full flex flex-col p-16 bg-[#1a1a1a]">
      <Typography
        variant="h2"
        className="w-full text-gray-100 p-16 text-center animate__animated animate__fadeInUp"
      >
        My Projects
      </Typography>
      {data ? (
        <div className="section-content-container">
          <Container className="w-full grid grid-cols-3 gap-8">
            {showProjectCards()}

            {!showMore && (
              <div>
                <Button className="mb-3" onClick={() => setShowMore(true)}>
                  show more
                </Button>
              </div>
            )}
          </Container>
        </div>
      ) : (
        <FallbackSpinner />
      )}
    </div>
  );
};

export default Projects;
